  font-weight: 300;
  overflow-y: scroll; }

body {
  min-width: 320px;
  font-size: 16px; }
  @media screen and (min-width: 768px) {
    body {
      font-size: 17px; } }

*, *::before, *::after {
  box-sizing: inherit; }

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #3e464c;
  letter-spacing: -.02em;
  line-height: 1.2;
  margin: 1em 0 .5em;
  font-weight: 300; }

h1, .h1 {
  font-size: 2.986em; }

h2, .h2 {
  font-size: 2.488em; }

h3, .h3 {
  font-size: 2.074em; }

h4, .h4 {
