    .flex-wrap.items-5 > * {
      max-width: 20%;
      flex-basis: 20%; }
    .flex-wrap [class*='item-'] {
      flex: none; }
    .flex-wrap .item-1-3 {
      width: 33.33333%; }
    .flex-wrap .item-2-3 {
      width: 66.66666%; }
    .flex-wrap .item-1-4 {
      width: 25%; }
    .flex-wrap .item-1-2, .flex-wrap .item-2-4 {
      width: 50%; }
    .flex-wrap .item-3-4 {
      width: 75%; }
    .flex-wrap .item-1-5 {
      width: 20%; }
    .flex-wrap .item-2-5 {
      width: 40%; }
    .flex-wrap .item-3-5 {
